.search-container {
    display: flex;
    justify-content: center;
    margin: 10px;
}

.search-input {
    padding: 8px 15px;
    font-size: 15px;
    width: 250px;
    border-width: 0;
    background-color: rgb(255, 255, 255);
    color:#48728c;
    border-radius: 50px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
}

.search-input::placeholder {
    color:#878f95;
}

.search-input:focus {
    outline: none;
}
